.container {
  padding-top: 20px;
}

.backButton {
  font-size: 20px;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 10px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backButton:hover,
.backButton:focus {
  background-color: #3a3634;
  color: white;
}
